body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav-list{
  display: flex;
  align-items: center;
  margin: 0 10px;
  background-color: #2F8F9D;
  border-radius: 10px;
  padding: 10px;
}

.nav-list .menu-items{
  margin-left: auto;
}

.nav-list .menu-items a{
  margin: 0 10px;
  gap: 1;
  text-decoration: none;
  padding: 4px;
  border: 1px solid black;
  border-radius: 4px;
  color: white;
  background-color: #82DBD8;
}

.nav-list .menu-items a:hover{
 
  background-color: #B3E8E5;
  color: white;
}

.nav-list .menu-items a.active {
  background-color: #0AA1DD;
  color: white;
} 

.btn {
  margin: 0 0.25rem;
  text-decoration: none;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid black;
  background-color: #82DBD8;
  color: white;
}

.btn:hover {
  background: #B3E8E5
} 

.add-form {
  max-width: 480px;
  margin: 0 auto;
}

.form-control {
  display: flex;
  flex-direction: column;
  margin: 0.5rem 0;
}

.form-control label {
  margin-bottom: 0.25rem;
}

.form-control input, .form-control textarea {
  flex: 1;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0.5rem;
}

button{
  border-radius: 8px;
}
button:hover{
  background-color: #82DBD8;
  
}