.todos-list {
    display: flex;
    flex-direction: column;
    padding: 20px;
    max-width: 600px;
    width: 600px;
    margin: 0 auto;
}

.todos-list article {
    box-sizing: border-box;
    border: 1px solid #ccc;
    margin-bottom: 15px;
    border-radius: 6px;
    padding: 10px;
    position: relative;
    box-shadow: 2px 2px grey;
    color: rgb(5, 118, 124);
    font-style: italic;
    text-align: center;
}

